.Card {
    width:210px;
    height: 300px;
    /* background: white; */
    box-shadow: 3px 3px 5px black;
    margin: 1%;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    overflow:hidden;
    border-radius: 4px;
    background: linear-gradient( hsl(120, 100%, 82%) 20%, white 60%);
    box-shadow: 5px 5px 10px -1px black;
    cursor: url(/Robo-friends/static/media/2.e2bf953e.cur), auto;
    transition:transform 0.25s;
}
.Card:hover {
    transform:scale(1.07) translate(-2px, -2px);
    box-shadow: 10px 10px 15px -3px black;
   }


.Card img {
    border: 5px solid rgba(255, 255, 255, 0.9);
    border-bottom: none;
    border-bottom: 2px solid hsl(120, 100%, 80%);
}

