.Card-desc p {
    overflow:hidden;
    padding: 4px 0;
}
.Card-desc > p:first-child {
    font-weight: 600;
}
.Card-desc > p:nth-of-type(2) {
    font-size: 0.87em;
}

@media screen and (max-width:600px) {
    .Card {
        width: 190px;
        margin: 2%;
    }
}
.CardDeck {
    max-width: 1250px;
    margin: 15px auto;
    display:flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.App {
  text-align: center;
  padding-top: 1%;
}

.App-header{
  font-family: Kalam;
  font-size: 55px;
  color:rgb(130, 72, 255);
  text-shadow: 0px 4px 1px rgba(0, 0, 0, 0.6);
}
.App-loader{
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}

.SearchBox {
    position: sticky;
    position: -webkit-sticky;
    top:20px;
    text-align: center;
    margin-bottom: 20px;
    font-size: 27px;
    z-index:2;
}
.SearchBox label {
    font-family:Kalam;
    color: rgb(47, 0, 122);
    cursor: inherit;
    padding: 0 30px 0 15px;
    border-left: 4px solid slateblue;
    border-radius: 3px;
    /* background: linear-gradient(to right, rgba(105, 90, 205, 0.1) 0%, rgba(0, 0, 0, 0.04) 100%); */
    transition: border-left 0.6s;
}

.SearchBox input {
    caret-color: rgb(0, 255, 255);
    width: 200px;
    /* margin: 0 2px; */
    font-size: 20px;
    padding: 10px 20px;
    border: none;
    box-shadow: inset 0 0 2px rgb(39, 39, 39);
    border-radius:  2px 20px 20px 2px;
    border-left: none;
    /* color: #555; */
    background-color: rgb(246, 246, 255);
    transition: box-shadow 0.5s, width 0.2s, background-color 1s;
}
.SearchBox input:focus {
    box-shadow: inset 0 0 4px rgb(0, 102, 185);
    width: 300px;  
    background-color: white;
    /* border-radius:  10px; */
}
.SearchBox:hover label {
    border-left: 4px solid rgba(105, 90, 205, 0);  
    /* background: none; */
}


